.quest{
    position: relative;
    max-width: 1000px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    row-gap: 15px;
}

.question{
    width: 100%;
    font-size: 24px;
    padding: 21px 0 21px;
    display: flex;
    justify-content: center;
}

.quest_images{
    width: 100%;
    display: flex;
    justify-content: center;
    /* padding-top: 80%; */
}

.quest_img{
    max-width: 765px;
    width: 100%;
    padding-top: 80%;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
}

.quest_images img{
    width: 100%;
}

.answer_container{
    width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
}

.answer_overlay{
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 2;
    transition: left 0.5s ease;
}

.ovr_hidden{
    left: -100%;
}

.ovr_container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 520px;
    width: 100%;
    background-color: white;
    border: 1px solid black;
    padding: 30px 10px;
    row-gap: 10px;
}

.mini_img{
    width: 100%;
    padding-top: 80%;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
}

.picture_info{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 10px;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 22px;
    font-weight: bold;
}

.ovr_container .btn_next{
    max-width: 400px;
    width:100%;
    padding: 15px 0 15px;
    border-radius: 10px;
    background-color: #29539b;
    background-image: linear-gradient(315deg, #29539b 0%, #1e3b70 74%);
    color: white;
    font-size: 24px;
    font-weight: bold;
    text-align: center;
    cursor: pointer;
    transition: transform 0.3s ease;
}

.btn_next:hover{
    transform: scale(1.05);
}

.btn_next:active{
    transform: scale(1);
}

.ok_error{
    width: 40px;
    height:40px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.ok{
    background-image: url('../../assets/svg/ok.svg');
}

.error{
    background-image: url('../../assets/svg/error.svg');
}